<template>
  <section class="withdrawal-history">
    <div class="withdrawal-history__filters">
      <div class="withdrawal-history__filters-main">
        <Input
          class="withdrawal-history__filters-main__search"
          hide-shadow
          :placeholder="t('common.search')"
        >
          <template #prepend>
            <Icon name="search" />
          </template>
        </Input>
        <SelectInput
          muted
          :placeholder="t('common.crypto')"
          icon="chevron-down"
          class="withdrawal-history__filters-main__crypto"
        />
        <SelectInput
          muted
          :placeholder="t('common.to')"
          icon="chevron-down"
          class="withdrawal-history__filters-main__to"
        />
        <SelectInput
          muted
          :placeholder="t('common.from')"
          icon="chevron-down"
          class="withdrawal-history__filters-main__from"
        />
      </div>
      <div class="withdrawal-history__filters-secondary">
        <RadioButtons v-model="withdrawalStatus" :data="radioFilter">
          <template #default="{ item }">
            {{ t(item.title) }}
          </template>
        </RadioButtons>
        <SelectColumns :columns="columns" />
      </div>
    </div>
    <Table :columns="columns" :data="data" expand>
      <template #column="{ column }">
        {{ t(column.title) }}
      </template>
      <template #cell="{ item, column }">
        <Badge
          v-if="column.type === 'status'"
          :type="COLORS[item]"
        >
          {{ t(STATUS[item]) }}
        </Badge>
        <Date
          v-else-if="column.type === 'date'"
          :date="item"
          format="DD MMM YYYY HH:mm"
        />
        <template v-else>{{ item }}</template>
      </template>
      <template #expand="{ item }">
        <div class="withdrawal-history__expand-table">
          <div class="withdrawal-history__expand-table__row">
            <Text type="muted" mr="20px">10:51 PM</Text>
            <Text>Transaction Initiated</Text>
          </div>
          <div class="withdrawal-history__expand-table__row">
            <Text type="muted" mr="20px">10:51 PM</Text>
            <Text>Signing transaction</Text>
          </div>
          <div class="withdrawal-history__expand-table__row">
            <Text type="muted" mr="20px">10:51 PM</Text>
            <Text>Transaction published with hash {{ item.hash }}</Text>
          </div>
        </div>
      </template>
    </Table>
  </section>
</template>

<script>
import { ref } from 'vue'
import { useActions, useGetters } from '@/shared/plugins/vuex'
import { useI18n } from '@/shared/plugins/i18n'
import Input from '@/shared/components/UIKit/Input'
import Icon from '@/shared/components/Icon/Icon'
import SelectInput from '@/shared/components/UIKit/Select/SelectInput'
import RadioButtons from '@/shared/components/UIKit/RadioButtons'
import SelectColumns from '@/admin/components/Table/SelectColumns'
import Table from '@/admin/components/Table/Table'
import Badge from '@/shared/components/UIKit/Badge'
import { COLORS, STATUS } from '@/shared/common/status'
import { radioFilter } from '@/admin/store/mock/withdrawalsMock'
import Text from '@/shared/components/UIKit/Text/Text'
import { MODULES_NAMES } from '@/shared/common/modules'
import Date from '@/shared/components/UIKit/Text/Date'

export default {
  name: 'WithdrawalHistory',
  components: {
    Date,
    Text,
    Table,
    SelectColumns,
    SelectInput,
    Input,
    Icon,
    RadioButtons,
    Badge
  },
  setup () {
    const { t } = useI18n()
    const withdrawalStatus = ref({})
    const {
      getHistoryColumns: columns,
      getHistoryData: data
    } = useGetters(MODULES_NAMES.WITHDRAWALS)
    const {
      setMockHistoryData
    } = useActions(MODULES_NAMES.WITHDRAWALS)

    setMockHistoryData()

    return {
      t,
      withdrawalStatus,
      radioFilter,
      columns,
      data,
      COLORS,
      STATUS
    }
  }
}
</script>

